import {action, makeObservable, observable, runInAction} from "mobx"
import LoginService from "../API/Authorization"

class UserStore{
    MyLogin = ''
    MyPassword = ''
    MyToken = null
    ServerIsAsked = false
    ErrorStatus = null
    MyName = ''
    MySername=''
    MyPatronymic = ''
    isUserLoginFree = false
    constructor(){
        makeObservable(this,{
            MyLogin: observable,
            MyPassword: observable,
            MyToken: observable,
            ServerIsAsked: observable,
            ErrorStatus: observable,
            MyName: observable,
            MySername: observable,
            MyPatronymic: observable,
            isUserLoginFree: observable,
            setMyLogin: action,
            setMyPassword: action,
            getMyToken: action,
            checkMyToken: action,
            clearMyToken: action,
            setErrorStatus: action,
            setMyName: action,
            setMySername: action,
            setMyPatronymic: action,
            checkUser: action,
            setUserLoginFree: action
        })
    }
    checkMyToken = () =>{
        if(localStorage.getItem('MyToken')){
            runInAction(()=>{
                this.MyToken = localStorage.getItem('MyToken')
            })
        }
    }
    clearMyToken = ()=>{
        localStorage.clear()
        this.MyToken = null
    }
    setErrorStatus = (Status)=>{
        this.ErrorStatus= Status
    }
    setMyLogin = (login)=>{
        this.MyLogin = login
    }
    setMyPassword = (password)=>{
        this.MyPassword = password
    }
    setMyName = (state)=>{
        this.MyName = state
    }
    setMySername = (state)=>{
        this.MySername = state
    }   
    setMyPatronymic = (state)=>{
        this.MyPatronymic = state
    }
    setUserLoginFree = (state)=>{
        this.isUserLoginFree = state
    }
    getMyToken = async ()=>{
        await LoginService.getToken(this.MyLogin, this.MyPassword)
            .then((response)=>{
                if(response.status===200){
                    runInAction(()=>{
                        this.MyToken = response.data.token
                        this.setMyLogin('')
                        this.setMyPassword('')
                        localStorage.setItem('MyToken', this.MyToken)
                    })
                }
            })
            .catch((error)=>{
                if(error.response.status===403){
                    this.setErrorStatus('   ')
                }
                else if(error.response.status===404){
                    this.setErrorStatus('     ')
                }
                else{
                    this.setErrorStatus('   ')
                }
            })
    }
    sendMyData = async ()=>{
        const MyData = {
            MyLogin: this.MyLogin,
            MyPassword: this.MyPassword,
            MyName: this.MyName,
            MySername: this.MySername,
            MyPatronymic: this.MyPatronymic
        }
        await LoginService.register(MyData)
            .then((response)=>{
                if(response.status===200){
                    runInAction(()=>{
                        this.getMyToken()
                    })
                }
            })
            .catch((error)=>{
                console.log(error);
                this.setErrorStatus('  ')
            })
    }
    checkUser = async ()=>{
        await LoginService.getUser(this.MyLogin)
            .then((response)=>{
                if(response.status===200){
                    this.isUserLoginFree=false
                    this.setErrorStatus('  ')
                }
            })
            .catch((error)=>{
                if(error.response.status===404){
                    this.isUserLoginFree=true
                }
                else{this.setErrorStatus(' ')}
            })
    }
}
export default new UserStore()